<template>
    <v-card class="mt-5 mx-auto" width="50%" elevation="10">
        <v-card-title class="text-h4 text-center mb-2 mt-2">TODOS</v-card-title>
        <v-divider></v-divider>
        <v-card-text class="mt-2">
            <v-row>
                <v-text-field v-model="newTodoContent" variant="outlined" placeholder="Add Todo"></v-text-field>
                <v-btn @click="addTodo" :disabled="!newTodoContent" class="ms-2" color="primary"
                    size="x-large">Add</v-btn>
            </v-row>

            <v-card class="mx-auto mt-5" :style="{ 'background-color': todo.done ? '#53B257' : 'white' }" elevation="5"
                width="100%" height="100%" v-for="todo in todos" :key="todo.id">
                <v-card-text>
                    <v-row>
                        <span class="my-auto text-h6">{{ todo.content }}</span>
                        <v-spacer></v-spacer>
                        <v-btn class="ms-2" color="success" elevation="6"
                            @click="toggleDone(todo.id)"><v-icon>mdi-check</v-icon></v-btn>
                        <v-btn class="ms-2" color="error" elevation="6"
                            @click="deleteTodo(todo.id)"><v-icon>mdi-delete</v-icon></v-btn>
                    </v-row>
                </v-card-text>
            </v-card>
        </v-card-text>
    </v-card>
</template>

<script>
/**
  * imports
*/

import { ref, onMounted } from 'vue';
import { collection, onSnapshot, addDoc, deleteDoc, doc, updateDoc, query, orderBy } from 'firebase/firestore';
import { db } from '@/firebase';

export default {
    name: "TodosV",

    setup() {


        /**
         * refs
         */

        const todosCollectionRef = collection(db, 'todos')
        const todosCollectionQuery = query(todosCollectionRef, orderBy('date', 'desc'));

        /**
   * todos
   */

        const todos = ref([
            // {
            //     id: "id1",
            //     content: "Shave my butt",
            //     done: false,
            // },
            // {
            //     id: "id2",
            //     content: "Wash my butt",
            //     done: false,
            // },
        ]);


        /**
         * get todos
         */

        onMounted(() => {
            onSnapshot(todosCollectionQuery, (querySnapshot) => {
                const fbTodos = [];
                querySnapshot.forEach((doc) => {
                    const todo = {
                        id: doc.id,
                        content: doc.data().content,
                        done: doc.data().done
                    }
                    fbTodos.push(todo)
                });
                todos.value = fbTodos
            })
        })


        /**
         * addTodo
         */

        const newTodoContent = ref('')

        const addTodo = () => {
            addDoc(todosCollectionRef, {
                content: newTodoContent.value,
                done: false,
                date: Date.now()
            })

            newTodoContent.value = ''
        }

        /**
         * deleteTodo
         */

        const deleteTodo = id => {
            deleteDoc(doc(todosCollectionRef, id));
        }

        /**
         * toggle done
         */

        const toggleDone = id => {

            const index = todos.value.findIndex(todo => todo.id === id)

            updateDoc(doc(todosCollectionRef, id), {
                done: !todos.value[index].done
            });
        }

        return {
            todos,
            newTodoContent,
            addTodo,
            deleteTodo,
            toggleDone,
        }
    }
}

</script>